<template>
    <div>
        <div class="kd-page-title">告知书</div>
        <div class="kd-page-content-wrapper kd-page-padding wrap-info">
            <div class="notify-content">
                <div>1、所处理的交通违法行为均为本人驾驶车辆</div>
                <div v-for="(item,index) in list" :key="index">
                    {{ index+2 }}、确认于<span class="line">{{ $momentFormat(item.illegalTime) }}</span>，在<span class="line">{{ item.illegalPlace }}</span>实施<span class="line">{{ item.wfnr }}</span>违法行为(代码<span
                    class="line"
                >{{ item.illegalAct }}</span>)，违反<span class="line">{{ item.fltw }}</span>，依据<span class="line">{{ item.wfgd }}</span>，拟予以<span class="line">{{ item.fines }}</span>元罚款，记<span class="line">{{ item.score }}</span>分。
                </div>
            </div>
        </div>
        <div class="notifyMsg">针对上述交通违法行为是否有异议，无异议视为本人放弃陈述申辩权，接受该处罚；有异议请至交通警察支队人工窗口咨询</div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="goUp"/>
            <kd-button icon="kedaIcon-shanchumr1" text="有异议" :button-handle="opposition" :but-disable="timeNum > 0"/>
            <kd-button icon="kedaIcon-duigou" text="无异议" :button-handle="nextStep" :but-disable="timeNum > 0"/>
            <span class="clock" v-if="timeNum > 0">({{ timeNum }})</span>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import audioCar from '@/assets/media/trafic/104.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'IllegalityElectronicMaanshanNotify',
    data () {
        return {
            countDown: null,
            timeNum: 8
        };
    },
    props: {
        list: { type: Array, default: () => [] }
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        this.countDown = setInterval(() => {
            this.timeNum--;
            if (this.timeNum <= 0) {
                clearInterval(this.countDown);
            }
        }, 1000);
    },
    beforeDestroy () {
        this.audioCar.destroy();
        clearInterval(this.countDown);
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        nextStep () {
            this.changeStep('down');
        },
        opposition () {
            this.changeTipPage({
                message: '请至' + this.CONSTANT.policeName + '人工窗口咨询',
                enableBack: true
            });
        },
        goUp () {
            this.changeStep('up');
        }
    }

};
</script>

<style lang="scss" scoped>
    .wrap-info {
        margin-bottom: 0;

        .notify-content {
            > div {
                line-height: 35px;
            }

            .line {
                border-bottom: 1PX solid #ffffff; /*no*/
            }
        }
    }

    .notifyMsg {
        padding-top: 10px;
        width: 84.37vw;
        margin: 0 auto;
        font-size: .98rem;
        color: red;
    }
</style>
