<template>
    <div>
        <div class="kd-page-title">机动车违法信息查询结果</div>
        <div class="kd-page-content-wrapper kd-table-style wrap-info">
            <el-table
                :data="pageData"
                ref="multipleTable"
                header-row-class-name="header-row"
                style="width: 100%"
                :highlight-current-row="true"
                @selection-change="selectionChange"
            >
                <el-table-column v-if="!isCheck" type="selection" width="50"/>
                <el-table-column width="160" label="违法时间">
                    <template slot-scope="scope">
                        {{ $momentFormat(scope.row.illegalTime) }}
                    </template>
                </el-table-column>
                <el-table-column prop="illegalPlace" label="违法地点" show-overflow-tooltip/>
                <el-table-column prop="wfnr" label="违法行为" show-overflow-tooltip/>
                <el-table-column prop="score" label="记分值" width="100"/>
                <el-table-column width="150" label="罚款金额">
                    <template slot-scope="scope">
                        <span>{{ '￥'+ scope.row.fines }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="90">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="medium">详情</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 总费用： -->
            <div class="kd-total-content">
                <div class="left">
                    <span @click="changePage(-1)"><i class="el-icon-caret-left"></i>上一页</span>
                    <span>{{currentPage}}/{{totalPage}}页</span>
                    <span @click="changePage(1)">下一页<i class="el-icon-caret-right"></i></span>
                </div>
                <div class="right" v-if="!isCheck">
                    <p style="margin-right: 13px;">共计</p>
                    <p>扣分<span style="margin-right: 13px;" class="total-info">{{ selection.length > 0 ? totalScore : 0 }}</span></p>
                    <p>处理罚款<span class="total-info">{{ selection.length > 0 ? totalFines : 0 }}</span>元</p>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="preStep"/>
            <kd-button icon="kedaIcon-circleright" v-if="!isCheck" text="下一步" :button-handle="nextStep" :but-disable="selection.length === 0 "/>
        </div>

        <el-dialog title="详情" :visible.sync="alertDetail" width="90%" :before-close="shutdownAlert">
            <div class="alertWrap">
                <div class="imgWrap"><img :src="currentImageSrc" alt="图片加载中......"/></div>
                <div>
                    <p>罚款金额：{{currentDetail.fines}}</p>
                    <p>法律条文：{{currentDetail.fltw}}</p>
                    <p>违反规定：{{currentDetail.wfgd}}</p>
                    <p>违法地点：{{currentDetail.illegalPlace}}</p>
                    <p>违法时间：{{$momentFormat(currentDetail.illegalTime)}}</p>
                    <p>违法内容：{{currentDetail.wfnr}}</p>
                    <p>监控编号：{{currentDetail.jkbh}}</p>
                    <p>记分值：{{currentDetail.score}}</p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import request from '@/utils/request';
import audioCar from '@/assets/media/trafic/9.mp3';
import { playAudio } from '@/utils/media';
import { LOCATION_ID } from '@/constant/constant';
import { getIllegalPic } from '@/service/common';

export default {
    name: 'illegality-electronic-bengbu-record-list',
    data () {
        return {
            currentImageSrc: {},
            currentDetail: {},
            alertDetail: false,

            detailBol: false,
            messageBoxArr: null,
            selection: [],
            currentPage: 1,
            defaultNum: 5, // 默认显示三条数据
            pageData: [],
            isChangePage: false// 翻页的时候，回触发变空
        };
    },
    props: {
        isCheck: Boolean,
        originList: { type: Array, default: () => [] },
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    computed: {
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode'
        }),
        totalPage () {
            return Math.ceil(this.originList.length / this.defaultNum) || 1;
        },
        totalScore () {
            let total = 0;
            this.selection.forEach((item) => {
                total += item.score;
            });
            return total;
        },
        totalFines () {
            let total = 0;
            this.selection.forEach((item) => {
                total += item.fines;
            });
            return total;
        }
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        this.changePage();
        this.isChangePage = false;
    },
    beforeDestroy () {
        this.audioCar.destroy();
        if (this.messageBoxArr) {
            this.messageBoxArr.close();
        }
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityElectronicSelectMonitor']),
        closeDetail () {
            this.detailBol = false;
        },
        preStep () { // 上一步
            this.changeStep('up');
        },
        async handleClick (item) {
            this.currentDetail = item;
            const url = request.getServerUrl() + '/ssm/carMgt/getMonitorPicture?xh=' + item.jkbh;
            if (this.CONSTANT.location === LOCATION_ID.yinchuan || this.CONSTANT.location === LOCATION_ID.zhongwei) {
                const res = await getIllegalPic({ xh: item.jkbh });
                if (res && res.code === '0') {
                    this.currentImageSrc = res.result;
                }
            } else {
                this.currentImageSrc = url;
            }
            this.alertDetail = true;
        },
        shutdownAlert () {
            this.currentImageSrc = '';
            this.currentDetail = {};
            this.alertDetail = false;
        },
        async nextStep () {
            await this.nextFunction({
                selection: this.selection,
                totalFines: this.totalFines,
                totalScore: this.totalScore
            });
        },
        changePage (type) {
            let page = this.currentPage;
            if (type === -1) {
                page = page - 1;
                if (page < 1) {
                    return;
                }
            } else if (type === 1) {
                page = page + 1;
                if (page > this.totalPage) {
                    return;
                }
            }
            this.currentPage = page;
            this.pageData = this.originList ? this.originList.slice(this.defaultNum * (page - 1), this.defaultNum * (page - 1) + this.defaultNum) : [];

            // 保留已选择的数据
            this.isChangePage = true;
            setTimeout(() => {
                this.selection.forEach((row) => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            });
        },
        selectionChange (selection) {
            if (this.isChangePage) {
                this.selection = this.selection.concat(selection);
            } else {
                this.selection = selection;
            }
            this.isChangePage = false;
        }
    }

};
</script>

<style lang="scss" scoped>
    .wrap-info {
        padding: 0;
    }

    .alertWrap {
        display: flex;

        .imgWrap {
            width: 50%;
            margin-right: 20px;

            > img {
                max-width: 100%;
                max-height: 100%;
            }
        }

        p {
            font-size: 18px;
            padding-bottom: 20px;
        }
    }
</style>
