<template>
    <div>
        <div class="kd-page-title">签名</div>
        <div class="kd-page-content-wrapper kd-page-padding wrap-info">
            <div>
                <img :src="img">
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="goUp"/>
<!--            <kd-button icon="kedaIcon-circleright" text="接受处罚" :button-handle="sure"/>-->
            <kd-button icon="kedaIcon-circleright" text="接受处罚" :button-handle="sure" :but-disable="!img"/>
        </div>
        <alert-msg
            v-if="alertOn"
            :alertSure="nextStep"
            :alertCancel="cancel"
            tip-text="是否确认违法"
        ></alert-msg>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { wsOnMessage, wsSend } from '@/utils/websocket';
import { addPrefix } from '@/utils/tools';
import audioCar from '@/assets/media/trafic/105.mp3';
import { playAudio } from '@/utils/media';
import alertMsg from '@/components/alert-msg';

export default {
    name: 'illegality-electronic-bengbu-sign',
    components: { alertMsg },
    computed: {
        ...mapState('permanent', {
            signWebSocket: 'signWebSocket'
        })
    },
    props: {
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } }
    },
    data () {
        return {
            img: '',
            alertOn: false
        };
    },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
        wsOnMessage(this.signWebSocket, this.getImg);
        wsSend(this.signWebSocket, {
            module: 'HwWriter',
            directive: 'Start'
        });
    },
    beforeDestroy () {
        this.audioCar.destroy();
        wsOnMessage(this.signWebSocket, null);
        wsSend(this.signWebSocket, {
            module: 'HwWriter',
            directive: 'Stop'
        });
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage', 'setPrintInfo']),
        ...mapMutations('illegality', ['updateSelectMonitor']),
        getImg (res) {
            if (res && res.code === 1) {
                if (res.directive === 'GetSignImage') {
                    this.img = addPrefix(res.result);
                } else if (res.directive === 'StopGetSignImage') {
                    this.img = addPrefix(res.result);
                    wsSend(this.signWebSocket, {
                        module: 'HwWriter',
                        directive: 'Stop'
                    });
                }
            }
        },
        sure () {
            this.alertOn = true;
        },
        cancel () {
            this.alertOn = false;
        },
        async nextStep () {
            await this.nextFunction(this.img);
            this.alertOn = false;
        },
        goUp () {
            this.changeStep('up');
        }
    }

};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        justify-content: center;
        align-items: center;

        > div {
            background: #ffffff;
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            > img {
                max-width: 100%;
                max-height: 100%;
                min-height: 100%;
                min-width: 100%;
            }
        }
    }
</style>
