<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process :type="1" v-if="currentStep === steps.findIndex(step=>step.key === 'process')" :handle-business="handleBusiness"/>
        <input-car v-if="currentStep === steps.findIndex(step=>step.key === 'choiceCar')" :nextFunction="inputCarInfo"/>
        <record-list v-if="currentStep === steps.findIndex(step=>step.key === 'recordList')" :isCheck="false" :originList="monitorList" :nextFunction="listFunction"/>
        <notify v-if="currentStep === steps.findIndex(step=>step.key === 'notify')" :list="selectMonitor.recordArr"/>
        <sign v-if="currentStep === steps.findIndex(step=>step.key === 'sign')" :nextFunction="signNext"/>
        <process :type="2" v-if="currentStep === steps.findIndex(step=>step.key === 'tickPrint')" :handle-business="tickPrint" waitText="业务凭条打印中，请稍后......"/>
        <process :type="3" v-if="currentStep === steps.findIndex(step=>step.key === 'synjdsbh')" :handle-business="synjdsbh" :waitText="synjdsbhText"/>
        <pay-qr-bb
            v-if="currentStep === steps.findIndex(step=>step.key === 'payQr')"
            :nextFunction="payResultCheck"
            :successlist="successArr"
            :failedlist="failedArr"
        />

        <process :type="3" v-if="currentStep === steps.findIndex(step=>step.key === 'processComplete')" :handle-business="processComplete"/>
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="completeResult"
            footTexts="温馨提示：请取走业务申请表或凭条"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import serverInfo from '@/components/server-info';
import inputCar from '@/view/pageComponents/input-car';
import recordList from '../../components/record-list';
import sign from './components/sign';
import payQrBb from '../../components/pay-qr-bb';
import complete from '@/components/complete';
import notify from './components/notify';
import { illegalityElectronicStart, illegalityElectronicFace, saveElectronicPhoto, illegalityElectronicCheckMonitor, checkHandleQualifications, illegalityElectronicSaveMonitorList, illegalityElectronicHandle, illegalityElectronicComplute, illegalityElectronicResultCheck } from '@/service/traffic/traffic';
import { ILLEGAL_ELECTRONIC_BENBU } from '@/constant/stepsOption';
import carMedia from '@/assets/media/trafic/18.mp3';
import driverMedia from '@/assets/media/trafic/17.mp3';
import { getElectronicMonitoringTick } from '@/constant/tickertape';
import { tickPrint } from '@/service/HardwareRequest';
import { cancelPrefix } from '@/utils/tools';
import { illegalityElectronicSelectTranslation, illegalityElectronicSelectUpdate } from '@/utils/dataTranlation';

export default {
    name: 'illegality-electronic-bengbu',
    components: { identifyCardPage, faceYitu, contrast, process, serverInfo, inputCar, recordList, sign, notify, payQrBb, complete },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            businessTypeCode: 'businessTypeCode',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('permanent', {
            tickWebSocket: 'tickWebSocket'
        })
    },
    data () {
        return {
            carMedia: carMedia,
            driverMedia: driverMedia,
            carLicense: '',
            carType: '',
            synjdsbhText: '',
            successArr: [],
            failedArr: [],
            monitorList: [],
            selectMonitor: { recordArr: [] },
            completeResult: {}
        };
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        async handleBusiness () {
            const res = await illegalityElectronicStart({
                idNum: this.identifyCard.certNumber
            });
            if (res && res.code === '0') {
                if (res.result.status === 1) {
                    await this.handleContrast();
                } else {
                    this.changeTipPage({ message: res.message });
                }
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleContrast () {
            const res = await illegalityElectronicFace({
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async payResultCheck () {
            const success = [];

            const monitorRecordIdList = [];

            this.successArr.forEach((item) => {
                const match = this.selectMonitor.recordArr.find((i) => {
                    return i.decisionNumber === item;
                });
                if (match) {
                    monitorRecordIdList.push(match.monitorRecordId);
                }
            });

            const res = await illegalityElectronicResultCheck({ monitorRecordIdList: monitorRecordIdList });
            if (res && res.code === '0') {
                res.result.forEach((item) => {
                    if (item.payStatus === 2) {
                        success.push(item);
                    }
                });
                if (success.length === this.successArr.length) {
                    this.changeStep('down');
                }
            }
        },
        async inputCarInfo ({ carType, carLicense }) {
            this.carLicense = carLicense;
            this.carType = carType;
            const res = await checkHandleQualifications({
                plateType: carType,
                plateNumber: this.CONSTANT.carTypeName + carLicense
            });
            if (res && res.code === '0') {
                await this.getList();
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async listFunction ({ selection, totalFines, totalScore }) {
            const res = await illegalityElectronicSaveMonitorList({
                ...selection[0],
                monitorRecordIds: selection.map((item) => {
                    return item.monitorRecordId;
                })
            });
            if (res && res.code === '0') {
                this.selectMonitor = illegalityElectronicSelectTranslation({
                    selection: selection,
                    list: res.result,
                    totalFines: totalFines,
                    totalScore: totalScore
                });
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async signNext (img) {
            const res = await saveElectronicPhoto({
                usersign: cancelPrefix(img),
                phototype: '1'
            });
            if (res && res.code === '0') {
                await this.signNextHandle(img);
            } else {
                this.changeTipPage({ message: res.message ? res.message : res.result.msg, enableBack: true });
            }
        },
        async signNextHandle (img) {
            const list = this.selectMonitor.recordArr;
            const res = await illegalityElectronicHandle({
                usersign: cancelPrefix(img),
                monitorRecordIds: list.map((item) => {
                    return item.monitorRecordId;
                })
            });
            if (res && res.code === '0') {
                this.selectMonitor = illegalityElectronicSelectUpdate(res.result, this.selectMonitor);
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        },
        async getList () {
            const res = await illegalityElectronicCheckMonitor({
                plateType: this.carType,
                plateNumber: this.carLicense
            });
            if (res && res.code === '0') {
                if (res.result.code === 1) {
                    this.monitorList = res.result.data;
                    this.changeStep('down');
                } else {
                    this.changeTipPage({ message: res.message ? res.message : res.result.msg, enableBack: true });
                }
            } else {
                this.changeTipPage({ message: res.message ? res.message : res.result.msg, enableBack: true });
            }
        },
        async synjdsbh () {
            this.successArr = [];
            this.failedArr = [];
            this.changeStep('down');
            this.monitorList.forEach((item) => {
                this.successArr.push(item.jdsbh);
            });
        },
        tickPrint () {
            this.selectMonitor.recordArr.forEach(async (item) => {
                const res = await tickPrint({ content: getElectronicMonitoringTick(item) });
                console.log('凭条打印结果', res);
            });
            this.changeStep('down');
        },
        async processComplete () {
            const res = await illegalityElectronicComplute({
                payStatus: 1,
                monitorRecordIds: this.selectMonitor.recordArr.map((item) => {
                    return item.monitorRecordId;
                })
            });
            if (res && res.code === '0') {
                this.completeResult = res.result;
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        }
    },
    mounted () {
        this.setSteps(ILLEGAL_ELECTRONIC_BENBU);
    }
};

</script>
